* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

li,
h2,
h3,
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.list-item {
  display: flex;
  gap: 20px;
  align-items: center;
}

.container {
  display: flex;
  gap: 40px;
  margin-bottom: 20px;
}
